.page {
  font-family: Inter, Arial, sans-serif;
  margin: 0;
  background-color: #000;
  width: 100%;
  color: #fff;
  text-rendering: optimizeLegibility;
  padding-top: 45px;
  position: relative;
  min-height: 100vh;
}

@media (max-width: 540px) {
  .page {
    padding-top: 28px;
  }
}
